import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-resumen-venta',
  templateUrl: './resumen-venta.page.html',
  styleUrls: ['./resumen-venta.page.scss'],
})
export class ResumenVentaPage implements OnInit {

  OV:any;
  datosVenta:any =[];
  venta:any =[];

  constructor(private loginService: LoginService,private activatedRoute:ActivatedRoute,) { }

  ngOnInit() {

    this.activatedRoute.params.subscribe(params=>{
      this.OV = params.ov;
      this.loginService.DetalledeVenta(this.OV).subscribe(resumen =>{
        this.datosVenta = resumen;

        this.venta = this.datosVenta.data;

      })

    })

   
    
  }

}
