  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 3em 0em;
  gap: 1em;
}
.resumenVentaDesk .contenido-cont .ov {
  color: #00a3c7;
  font-weight: 100;
  font-size: 2.4em;
  margin-top: 0px;
  margin-bottom: 0px;
  margin-block-start: 0em;
  margin-block-end: 0em;
}
.resumenVentaDesk .contenido-cont .txt-r {
  color: #373737;
  font-size: 1.4em;
  font-weight: 100;
}
.resumenVentaDesk .contenido-cont .compra-cont {
  width: 35em;
  height: auto;
  background: #ffffff;
  border-radius: 2em;
  box-shadow: rgba(0, 163, 199, 0.41) 0px 4px 29px 0px;
  border: 1px solid rgba(0, 163, 199, 0.2117647059);
