  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 1em;
  padding: 1.5em 2em;
}
.resumenVentaDesk .contenido-cont .compra-cont .line {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 1em;
}
.resumenVentaDesk .contenido-cont .compra-cont .line .qe {
  color: #00a3c7;
  font-size: 0.97em;
  font-weight: 100;
  width: 30%;
}
.resumenVentaDesk .contenido-cont .compra-cont .line .result {
  color: #00a3c7;
  font-size: 0.9em;
  font-weight: 100;
  width: 70%;
}

@media (min-width: 992px) {
  ion-header {
    display: none;
  }
  ion-content {
    display: none;
  }
  .resumenVentaDesk {
    display: flex;
  }
}
.resumenVentaDesk::-webkit-scrollbar {
  width: 0.35em;
  background-color: transparent;
  height: 0.7em;
}

